import Opening from "./compenents/Opening";
import Animated from "./compenents/Animated";

import "./project.css"
import CertComp from "./compenents/CertComp";
import "react-responsive-carousel/lib/styles/carousel.min.css";
import { Carousel } from 'react-responsive-carousel';
const reactNative = new URL("/assets/ReactNative.jpg", import.meta.url);
const reactNative2 = new URL("/assets/ReactNative2.jpg", import.meta.url);
const certs = [
    {

        id: 1,
        title: 'React Native',
        date: 'April 2023',
        credential: 'R36NW77SMMH4',
        info: 'Building a single-page React Native app and styling it using basic components. Building large lists and configuring user inputs within a React Native appUsing the Pressable component to build buttons and other clickable areas. Setting up an app with React Navigation and moving between screens',
        link: 'https://www.coursera.org/account/accomplishments/certificate/7EANQHA8WPAK',
        image: reactNative2
    }, {

        id: 2,
        title: 'React Basics',
        date: 'April 2023',
        credential: 'R36NW77SMMH4',
        info: 'Use reusable components to render views where data changes over time Organize React projects to create more scalable and maintainable websites and apps Use props to pass data between components.Create dynamic and interactive web pages and apps Use forms to allow users to interact with the app.Build an application in React',
        link: 'https://www.coursera.org/account/accomplishments/certificate/R36NW77SMMH4',
        image: reactNative
    }
];


function Certifications() {
    return (
        <Animated>
            <section id="Certifications">

                <Opening Name="Certifications" intro="Current Certifications" />
                <Carousel infiniteLoop >
                    {certs.map((cert) =>
                        <CertComp key={cert?.id} title={cert?.title} link={cert?.link} info={cert?.info} image={cert?.image} />
                    )}
                </Carousel>

        </section>
    </Animated>
    );
};
export default Certifications;
