function About() {


	return (<Animated>
		<section id="About">

			<Opening Name="About me" intro="Get to know me." p1="Slide left or right to discover the topics about me. " />

			<Carousel infiniteLoop  >
				{slides.map((slide) => (
					<div>{slide.module}</div>
				))}
			</Carousel>

		</section>
	</Animated>
	);
};
export default About;
