import classes from './Certs.module.css';
function CertComp(props) {
    return (
        <div className={classes.frame}>
            <div className={classes.contactI} >
                <div className={classes.sectionTitle}>
                <h1>{props.title}</h1>

                    <a href={props.link} >
                        Click here to open in a new tab
                    </a>
                <p>
                    {props.info}
                </p>
                </div>
                <div className={classes.sectionImage}>
                    <img alt='none' title={props.title} src={props.image} />
                </div>


            </div>

        </div>
    );
}

export default CertComp;
