
import Opening from "./compenents/Opening";
import classes from "./HomeLayout.module.css";
import HomeI from "./compenents/HomeI";
import Animated from "./compenents/Animated";

const ani = new URL("./ProPic.jpg", import.meta.url);
function Home(){
    return(<Animated>
    <section id="Home" className={classes.Infos}>

            <Opening intro="Hi my Name is," Name="Esau Lopez" p1="Software Engineer, based in the Phoenix Metro Area." />

        <div className={classes.grid}>
        <HomeI Head="Welcome!" image={ani} par="This website is designed using React and hosted using Github Pages."/>


       </div>

    </section>
    </Animated>
);
};
export default Home;
