import classes from "./Info.module.css";

function Info(props) {
	return (


		<div className={classes.info1}>
				<div><h1>{props.text}</h1></div>
			<div className={classes.card}>
				<div className={classes.in}><img src={props.img} alt="" ></img></div>
				<div className={classes.il}><p>{props.info1}</p></div>
				</div>

			</div>


	);
}

export default Info;
